var React   = require('react');
var _       = require('underscore');

var HelpRequest = require('./HelpRequest.jsx');

module.exports = React.createClass({displayName: 'exports',
    getInitialState: function() {
        return {
            unassignedExpanded: false,
            closedExpanded: false
        };
    },
    toggleExpandMore: function(type) {
        return function(e) {
            e.preventDefault();

            switch(type) {
            case "unassigned":
                return this.setState({unassignedExpanded: !this.state.unassignedExpanded});
            case "closed":
                return this.setState({closedExpanded: !this.state.closedExpanded});
            }
        };
    },
    renderRequestList: function(requests, opts) {
        if (typeof opts === 'undefined') opts = {};

        return (
            React.createElement("ul", null, 
                _.map(requests, function(req) {
                    var elem = React.createElement(HelpRequest, {helpers: this.props.helpers, request: req, 
                        refresh: this.props.refresh});
                    if (opts.inProgress) {
                        elem.props.progress = true;
                    }
                    return (React.createElement("li", null, elem));
                 }.bind(this))
            )
        );
    },
    renderAssigned: function() {
        var elem;
        if (_.isEmpty(this.props.requests.assigned)) {
            elem = React.createElement("p", null, "No assigned requests.");
        } else {
            elem = this.renderRequestList(this.props.requests.assigned, {inProgress: true});
        }

        return (
            React.createElement("div", {className: "request-list requests-assigned"}, 
                React.createElement("h4", null, "In progress"), 
                elem
            )
        );
    },
    renderUnassigned: function() {
        var elems;
        if (_.isEmpty(this.props.requests.unassigned)) {
            elems = [React.createElement("p", null, "No unassigned requests!")];
        } else {
            var moreClass = "requests-more";
            var toggleText;
            if (!this.state.unassignedExpanded) {
                moreClass += " hidden";
                toggleText = "More requests...";
            } else {
                toggleText = "Fewer requests";
            }

            elems = [
                (React.createElement(HelpRequest, {helpers: this.props.helpers, 
                              request: _.first(this.props.requests.unassigned), 
                              refresh: this.props.refresh, 
                              highlight: true}))
            ];
            if (!_.isEmpty(_.tail(this.props.requests.unassigned))) {
                elems = elems.concat([
                    (React.createElement("a", {href: "", 
                        onClick: this.toggleExpandMore("unassigned").bind(this)}, toggleText)),
                    (React.createElement("div", {className: moreClass}, 
                        this.renderRequestList(_.tail(this.props.requests.unassigned))
                    ))
                ]);
            }
        }

        return (
            React.createElement("div", {className: "request-list requests-unassigned"}, 
                React.createElement("h4", null, "Up next:"), 
                elems
            )
        );
    },
    renderClosed: function() {
        var elems;
        if (_.isEmpty(this.props.requests.closed_recently)) {
            elems = [React.createElement("p", null, "No recently closed requests.")];
        } else {
            requestList = this.renderRequestList(this.props.requests.closed_recently);

            var moreClass = "requests-more";
            var toggleText;
            if (!this.state.closedExpanded) {
                moreClass += " hidden";
                toggleText = "Expand...";
            } else {
                toggleText = "Collapse";
            }

            elems = [
                React.createElement("a", {href: "", onClick: this.toggleExpandMore("closed").bind(this)}, toggleText),
                React.createElement("div", {className: moreClass}, 
                    requestList
                )
            ];
        }

        return (
            React.createElement("div", {className: "request-list requests-closed"}, 
                React.createElement("h4", null, "Recently closed"), 
                elems
            )
        );
    },
    render: function() {
        var requestsElem;

        if (this.props.requests === null) {
            requestsElem = (React.createElement("span", null, "Loading..."));
        } else {
            requestsElem = (
                React.createElement("div", {className: "help-requests-container"}, 
                    this.renderUnassigned(), 
                    this.renderAssigned(), 
                    this.renderClosed()
                )
            );
        }

        return (
            React.createElement("div", {className: "content-pane help-requests"}, 
                requestsElem
            )
        );
    }
});
