var React   = require('react');
var _       = require('underscore');
var moment  = require("moment");

var Api = require("./Api");
var AssignHelperForm = require("./AssignHelperForm.jsx");

module.exports = React.createClass({displayName: 'exports',
    resolveRequest: function(reason) {
        Api.HelpRequests.resolve(this.props.request.id, reason).then(function() {
            this.props.refresh();
        }.bind(this), function(err) {
            // TODO: handle error
            alert(err);
        });
    },
    assignRequest: function(verb, helper_id) {
        if (typeof helper_id === "string") helper_id = parseInt(helper_id);

        Api.HelpRequests[verb](this.props.request.id, helper_id).then(function() {
            this.props.refresh();
        }.bind(this), function(err) {
            // TODO: handle error
            alert(err);
        });
    },
    availableHelpers: function() {
        return _.filter(this.props.helpers, function(h) {
            return !h.help_request;
        });
    },
    render: function() {
        var request = this.props.request;
        var helper;

        var assignElems = [];
        if (request.helper) {
            helper = request.helper.person;
            var resolveRequestHandler = function(reason, e) {
                e.preventDefault();
                this.resolveRequest("resolved");
            };

            assignElems.push(
                React.createElement("button", {className: "btn btn-default", 
                    onClick: resolveRequestHandler.bind(this, "left")}, "Student left"));
            assignElems.push(
                React.createElement("button", {className: "btn btn-primary", 
                    onClick: resolveRequestHandler.bind(this, "resolved")}, "Resolved"));
            assignElems.push(React.createElement(AssignHelperForm, {
                availableHelpers: this.availableHelpers(), 
                callback: this.assignRequest.bind(this, 'reassign'), 
                prompt: "Reassign to...", verb: "reassign"}));
        } else {
            var prompt, verb, callback;
            if (this.props.request.open) {
                prompt = "Assign to...";
                verb = "assign";
                callback = this.assignRequest.bind(this, 'assign');
            } else {
                prompt = "Reopen and assign to...";
                verb = "reopen";
                callback = this.assignRequest.bind(this, 'reopen');
            }
            assignElems.push(React.createElement(AssignHelperForm, {
                availableHelpers: this.availableHelpers(), 
                callback: callback, 
                prompt: prompt, verb: verb}));
        }

        var elems = [
            React.createElement("div", {className: "row"}, 
                React.createElement("div", {className: "row-items col-lg-4 col-md-2"}, 
                    React.createElement("span", {className: "request-student"}, 
                        request.person.first_name + " " + request.person.last_name
                    ), 
                    React.createElement("span", {className: "request-course"}, 
                        request.course.code
                    )
                ), 
                React.createElement("div", {className: "row-items assign-btns col-md-10 col-lg-8"}, 
                    assignElems
                )
            )
        ];

        elems = elems.concat([
            React.createElement("div", {className: "request-description"}, 
                React.createElement("p", null, request.description)
            ),
            React.createElement("div", null, 
                React.createElement("span", {className: "request-location"}, 
                    "Location: " + request.location
                ), 
                React.createElement("span", {className: "request-timestamp"}, 
                    moment(request.created_at).format("h:mm A")
                )
            )
        ]);

        if (helper) {
            elems.push(React.createElement("div", null, React.createElement("span", {className: "request-assignment"}, 
                helper.first_name + " " + helper.last_name + " is helping"
            )));
        }

        var className = "clearfix help-request";
        if (this.props.highlight) {
            className += " bg-success";
        }
        if (this.props.progress) {
            className += " bg-warning";
        }

        return (
            React.createElement("div", {className: className}, 
                elems
            )
        );
    }
});
