var React   = require('react');
var Api     = require('./Api');
var _       = require('underscore');

module.exports = React.createClass({displayName: 'exports',
    handleSignIn: function(e) {
        e.preventDefault();

        var formItem = this.refs.sunet_id.getDOMNode();
        var personId = formItem.value;

        Api.Helpers.checkin(personId).then(function(helper) {
            formItem.value = "";
            helper = helper.data;
            var helpers = this.props.helpers;

            // Don't add immediately; checkin is idempotent, so it succeeds if the helper is
            // already checked in.
            var notPresent = (_.find(helpers, function(h) {
                return h.id == helper.id;
            }) === undefined);
            if (notPresent) {
                helpers = _.clone(this.props.helpers);
                helpers.push(helper);
            }

            this.props.refresh(helpers);
        }.bind(this), function(err) {
            // TODO: real error handling
            alert(JSON.stringify(err));
        });
    },
    handleSignOut: function(helper_id, e) {
        Api.Helpers.checkout(helper_id).then(function() {
            this.props.refresh(_.reject(this.props.helpers, function(h) {
                return h.id == helper_id;
            }));
        }.bind(this), function(err) {
            // TODO: real error handling
            alert(JSON.stringify(err));
        });
    },
    renderHelper: function(helper) {
        var elems = [];

        if (this.props.staff) {
            var className = "helper-sign-out list-btn";
            var handler;
            if (helper.help_request) {
                className += " invisible";
                handler = function(e) { e.preventDefault(); };
            } else {
                handler = this.handleSignOut.bind(this, helper.id);
            }
            elems.push(
                React.createElement("a", {href: "#", className: className, onClick: handler}, 
                    React.createElement("span", {className: "glyphicon glyphicon-remove", 'aria-hidden': "true"})
                ));
        }
        elems.push(
            React.createElement("span", {className: "active-helper-name"}, 
                helper.person.first_name + " " + helper.person.last_name
            )
        );

        if (this.props.staff && helper.help_request) {
            elems.push(React.createElement("span", {className: "active-helper-status"}, "Busy"));
        }
        return React.createElement("li", null, elems);
    },
    render: function() {
        var elems = [];

        if (this.props.helpers === null) {
            elems.push(React.createElement("span", null, "Loading..."));
        } else {
            elems.push(
                React.createElement("ul", {className: "helper-list"}, 
                    _.map(this.props.helpers, this.renderHelper)
                )
            );
        }

        if (this.props.staff) {
            elems.push(
                React.createElement("form", {className: "helper-sign-in", onSubmit: this.handleSignIn}, 
                    React.createElement("input", {type: "text", className: "form-control", ref: "sunet_id", 
                        placeholder: "SUNet ID"}), 
                    React.createElement("input", {className: "btn btn-primary", type: "submit", value: "Sign in"})
                )
            );
        }

        return (
            React.createElement("div", {className: "active-helpers content-pane"}, 
                elems
            )
        );
    }
});
