var React   = require('react');
var _       = require('underscore');

module.exports = React.createClass({displayName: 'exports',
    renderHelperOption: function(h) {
        var handler = function(value) {
            return function(e) {
                e.preventDefault();
                this.props.callback(value);
            }.bind(this);
        }.bind(this);
        return (
            React.createElement("li", null, React.createElement("a", {href: "#", onClick: handler(h.id)}, 
                h.person.first_name + " " + h.person.last_name
            )));
    },
    enableTooltip: function() {
        if (this.refs.assignButton) {
            $(this.refs.assignButton.getDOMNode()).tooltip();
        }
    },
    componentDidMount: function() {
        this.enableTooltip();
    },
    componentDidUpdate: function() {
        this.enableTooltip();
    },
    render: function() {
        var assignButton;
        if (_.isEmpty(this.props.availableHelpers)) {
            assignButton = (
                React.createElement("div", {ref: "assignButton", 'aria-expanded': "false", 'data-toggle': "tooltip", 
                    'data-placement': "top", className: "tooltip-wrapper", 
                    title: "Can't " + this.props.verb + "; all helpers busy!"}, 
                    React.createElement("button", {type: "button", className: "btn btn-default", disabled: "true"}, 
                        this.props.prompt
                    )
                ));
        } else {
            // disable tooltip if necessary
            if (this.refs.assignButton) {
                $(this.refs.assignButton.getDOMNode()).tooltip('hide');
            }

            assignButton = (
                React.createElement("button", {type: "button", className: "btn btn-default dropdown-toggle", 
                    ref: "assignButton", 'data-toggle': "dropdown", 'aria-expanded': "false"}, 
                    this.props.prompt
                ));
        }

        return (
            React.createElement("div", {className: "assign-helper"}, 
                React.createElement("div", {className: "btn-group"}, 
                    assignButton, 
                    React.createElement("ul", {className: "dropdown-menu", role: "menu"}, 
                        _.map(this.props.availableHelpers, this.renderHelperOption)
                    )
                )
            )
        );
    }
});
